/* main  */

.main {
    padding: 4em;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

h1 {
    font-family: 'Tajawal', sans-serif;
    text-align: end;
    color: #555;
    margin-right: 1.5em;
    cursor: pointer;
}

.menu {
    text-align: end;
    background-color: #fff;
    padding: .5em;
    opacity: .7;
    position: fixed;
    z-index: 10000000;
    top: 7em;
    right: 3em;
    white-space: nowrap;
}

a {
    text-decoration: none;
    color: #000;
}

.menuItem {
    padding: .5em;
    cursor: pointer;
}

.menuItem:hover {
    background-color: #ccc;
}

.menuCloser {
    cursor: pointer;
}

footer {
    text-align: center;
}

/* tiles  */

.tiles-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2em;
}

.art-image-container {
    max-width: 300px;
    max-height: 400px;
    align-self: baseline;
}

.art-image {
    width: 100%;
    max-height: 500px;
    cursor: pointer;
}

.image-buttons {
    opacity: .7;
    margin-top: -2.8em;
}

.image-buttons:hover {
    opacity: 1;
}

button {
    color: #fff;
    background-color: #888;
    padding: .5em;
    border-radius: 5px;
    border: 1px solid #888;
    font-size: 1.2em;
    opacity: .9;
    /* margin-top: -5em; */
}

.modal-image {
    position: fixed;
    z-index: 100000010;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 5px 5px 20px #222;
    max-width: 50%;
    max-height: 80%;
    overflow: scroll;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.modal-image-description {
    height: 60%;
    width: 60%;
    background-color: #333;
    color: #fff;
    padding: 2em;
    opacity: 0.9;
    cursor: pointer;
}

.modal-overlay {
    z-index: 100000000;
    background-color: #353A40;
    opacity: .7;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 800px) {
    .modal-image {
        max-width: 90%;
        max-height: 90%;
    }
}